*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted buttontext;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --header-white-color: #fffdfa;
  --black-main-text-color: #000;
  --black-secondary-text-color: #0d0d0e;
  --black-button-color: #262525;
  --select-bg-color: #f8f3ed;
  --border-grey: #d2d2d2;
  --brown-gradient: linear-gradient(91deg, #7e7b79 0.66%, #524d45 28.33%, #211e1e 100.76%);
  --footer-background-color: linear-gradient(0deg, #0003 0%, #0003 100%), #3a3838;
  --achievements-background-color: linear-gradient(92deg, #918f8d 0.35%, #d9a96e 100%);
  --feedback-background-color: linear-gradient(91deg, #cfc9c2 0.16%, #fffaf4 100.76%);
  --gradient-service-img: linear-gradient(0deg, #877d7d66 0%, #877d7d66 100%);
  --main-white-color: #fff;
  --modal-white-color: #fcfeff;
  --button-white-color: #e7e7e7;
  --main-order-gradient: linear-gradient(91deg, #cfc9c2 0.16%, #fffaf4 100.76%);
  --bronze-button: #b4721d;
  --transition-speed: .8s cubic-bezier(.4, 0, .2, 1);
  --card-shadow: 0px 1px 6px #2e2f4214, 0px 1px 1px #2e2f4229, 0px 2px 1px #2e2f4214;
}

body {
  font-family: Roboto, sans-serif, Raleway;
  font-size: 14px;
  font-style: normal;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, li, a, p {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

img {
  display: block;
}

header {
  z-index: 900;
  opacity: .9;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

main {
  padding-top: 73px;
}

.header-container {
  border-bottom: 1px solid var(--header-white-color);
  background: var(--brown-gradient);
  justify-content: space-between;
  align-items: center;
  padding: 14px 15px;
  display: flex;
}

.header-link {
  color: #0000;
  text-decoration: none;
}

.header-link a:hover {
  color: inherit;
}

.name-logo {
  flex-flow: column wrap;
  align-items: center;
  display: flex;
}

.igor {
  color: var(--header-white-color);
  letter-spacing: .36px;
  text-transform: uppercase;
  font-family: Merriweather;
  font-size: 18px;
  line-height: 1.4;
}

.header-logo {
  align-items: center;
  gap: 4px;
  display: flex;
}

.line-left {
  color: var(--header-white-color);
  width: 37px;
  height: 1px;
}

.line-right {
  color: var(--header-white-color);
  width: 45px;
  height: 1px;
}

.label {
  color: var(--header-white-color);
  letter-spacing: 2.88px;
  font-family: Roboto;
  font-size: 12px;
  line-height: 1.4;
}

.scale-icon {
  fill: var(--header-white-color);
}

.mobile-menu-open {
  background-color: #0000;
  border: none;
  padding: 0;
  line-height: 0;
}

.burger-menu svg {
  fill: var(--header-white-color);
  width: 24px;
  height: 24px;
}

@media screen and (width >= 1280px) {
  .mobile-menu {
    display: none;
  }
}

@media screen and (width <= 1279px) {
  .mobile-menu {
    z-index: 1000;
    visibility: hidden;
    opacity: .9;
    pointer-events: none;
    transition: transform var(--transition-speed), visibility .5s linear, opacity .5s linear;
    background: linear-gradient(0deg, #000000d9 0%, #000 100%), #d3d3d3 url("mob-menu-w320.6ebc2ebb.webp") 50% / cover no-repeat;
    width: 100%;
    height: 100%;
    padding: 71px 36px;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-100%);
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translateX(0);
  }

  .mobile-menu_close-btn {
    background-color: #0000;
    border: none;
    padding: 0;
    position: absolute;
    top: 22px;
    right: 15px;
  }

  .list-mobile_nav {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
    margin-top: 50px;
    margin-bottom: 50px;
    display: flex;
  }

  .mobile-menu_link {
    color: var(--header-white-color);
    font-family: Roboto;
    line-height: 1.4;
  }

  .order-btn {
    border: 1px solid var(--Button, #faac39);
    color: var(--header-white-color);
    background-color: #0000;
    border-radius: 8px;
    padding: 14px 30px;
  }
}

footer {
  background: var(--footer-background-color);
}

.footer-container {
  flex-direction: column;
  align-items: center;
  gap: 24px;
  width: 320px;
  margin: 0 auto;
  padding: 14px 16px;
  display: flex;
}

.all-rigts h3, .socials h3, .logo-slogan {
