  letter-spacing: .1px;
  font-family: Roboto;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
}

.socials h3 {
  text-align: center;
  margin-bottom: 8px;
}

.logo-slogan {
  text-align: center;
  margin-top: 8px;
}

.social-icons {
  align-items: flex-start;
  gap: 20px;
